<template>
    <div>
        font size
        <div class="test">
{{test}}
        </div>
        <input type="button" value="点击我Demo" @click="updateDemoData({name:'change',age:30})"/>
        <router-view></router-view>
    </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
    
    computed: {
        ...mapGetters({
            test:"test"
        })
    },
    methods: {
        ...mapActions([
          "updateDemoData"
        ])
    }
}
</script>

<style lang="less" scoped>
@import "../assets/css/demo.less";
</style>




